  max-width: 350px;
  padding: 1.875rem;
  text-align: center;
  position: relative;
  box-sizing: border-box;
}

.chuck__promo-title {
  font-size: 1.5em;
  font-weight: 700;
  margin: 0 0 0.9375rem;
}

.chuck__promo-coffee-btn-outer {
  display: block;
  height: 3.75rem;
  margin: auto;
  position: relative;
  width: 3.75rem;
}
.chuck__promo-coffee-btn-outer::after {
  animation: coffee linear 6s both infinite;
  border: 2px dashed #07c;
  border-radius: 50%;
  content: "";
  height: 4.0625rem;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 4.0625rem;
  z-index: -1;
}
.chuck__promo-coffee-btn-outer:hover::after {
  animation-play-state: paused;
}

.chuck__promo-coffee-btn {
  background: url("../img/maquetando-con-chuck/btn-kofi.svg") left top no-repeat;
  border-radius: 50%;
  box-shadow: 0 0.125rem 10px rgba(0, 0, 0, 0.2);
  display: block;
  height: 0.0625rem;
  overflow: hidden;
  padding-top: 3.6875rem;
  width: 3.75rem;
}

@keyframes coffee {
  100% {
    transform: translate(-50%, -50%) rotate(180deg);
  }
}
.chuck__current-coffees {
  color: #8a6140;
  font-size: 0.75em;
  transform: translateY(1em);
}

.chuck__footer {
  background: #008aff;
  border-radius: 0.25rem 0.25rem 0 0;
  padding: 0.75rem;
  text-align: center;
  color: #fff;
  margin-top: 1.875rem;
}

.chuck__license {
  margin: 0 0 0.3125rem;
}

.chuck__cc {
  height: 0.0625rem;
  overflow: hidden;
  padding-top: 0.9375rem;
  display: inline-block;
  vertical-align: -0.125rem;
  background: url("../img/maquetando-con-chuck/ico-license.svg") left top no-repeat;
  width: 2.8125rem;
  opacity: 0.8;
}

.chuck__author {
  color: inherit;
  text-decoration: none;
}

.chuck__endline {
  margin: 0;
  font-size: 0.85em;
  font-weight: 300;
}